import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import NewSignal from '@/components/NewSignal'
import ViewSignal from '@/components/ViewSignal'
import EditSignal from '@/components/EditSignal'
import Login from '@/components/Login'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/new-signal',
      name: 'new-signal',
      component: NewSignal
    },
    {
      path: '/view-signal/:signal_id',
      name: 'view-signal',
      component: ViewSignal
    },
    {
      path: '/edit-signal/:signal_id',
      name: 'edit-signal',
      component: EditSignal
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    
  ]
})



