<template>
    <div id="view-signal">
        <ul class="collection with-header center">
            <li class="collection-header " style="background-color:grey">
                <img v-bind:src="A_flag" alt="">
                {{Currency}}
                <img v-bind:src="B_flag" alt="">
            </li>
            <li class="collection-item">
                Duration : {{Duration}} Minute
            </li>
            <li class="collection-item">
                Action : {{Action}}
            </li>
            <li class="collection-item">
                Result : {{Result}}
            </li>
            <li class="collection-item">
                User Group : {{gp}}
            </li>
            <li class="collection-item">
                <router-link to="/" class="btn btn-default">
                Back
                </router-link>
                
                <button v-on:click="deletesignal" class="btn btn-danger">
                Delete
                </button>
            </li>
        </ul>
    </div>
</template>
<script>
import db from './firebaseInit'
export default {
    name: 'view-signal',
    data() {
        return {
            id : null,
            A_flag :  null,
            B_flag :  null,
            Action :  null,
            Currency : null,
            Duration : null,
            Result : null,
            Type : null,
            enable : null,
            gp : null
        }
    },
    
    beforeRouteEnter (to, from, next) {
        var docRef = db.collection("Signals").doc(to.params.signal_id);
        
        docRef.get().then(doc => {
            console.log(doc.id)
            next(
                vm => {
                            vm.id = doc.id,
                            vm.A_flag =  doc.data().A_flag
                            vm.B_flag =  doc.data().B_flag
                            vm.Action =  doc.data().Action
                            vm.Currency = doc.data().Currency
                            vm.Duration = doc.data().Duration
                            vm.Result = doc.data().Result
                            vm.Type = doc.data().Type
                            vm.enable = doc.data().enable
                            vm.gp = doc.data().gp
                 
                }
            )
        })   
    },
    watch:{
        '$route' : 'fetchData'
    },
    methods:{
        fetchData(){
            var docRef = db.collection("Signals").doc(this.id);
        docRef.get().then(function(doc) {
            if (doc.exists) {
                console.log("Document data:", doc.data());
                doc=>{
                    next(
                        vm=>{
                            this.id = doc.id,
                            this.A_flag =  doc.data().A_flag
                            this.B_flag =  doc.data().B_flag
                            this.Action =  doc.data().Action
                            this.Currency = doc.data().Currency
                            this.Duration = doc.data().Duration
                            this.Result = doc.data().Result
                            this.Type = doc.data().Type
                            this.enable = doc.data().enable
                            this.gp = doc.data().gp
                        },
                    )
                }
            } else {
                // doc.data() will be undefined in this case
                console.log("No such document!");
            }
        })
        },
        deletesignal (){
            if(confirm('Are you sure?')){
            var docRef = db.collection("Signals").doc(this.id);
            docRef.delete()
            this.$router.push('/')
            }
        }
    }
    
}
</script>


