<template>
    <div id="dashboard">
        <ul class="collection with-header" style="background-color:grey">
            <li class="collection-header text-center center">
                <h4>Signals</h4>
            </li>
            <li v-if="active_signal != false" class="collection-item" style="background-color:grey">
                <div class="row col-md-12">
                    <div class="col-md-9 col-sm-12 col-xs-12" >
                        <img v-bind:src="active_signal.A_flag" alt="">
                        {{active_signal.Currency}}
                        <img v-bind:src="active_signal.B_flag" alt="">
                    </div>
                    
                    <div class="col-md-3 col-sm-12 col-xs-12 secondary-content text-right right end">
                        <form @submit.prevent="updatesignal">
                            <div class="col-md-6 col-sm-12 col-xs-12 secondary-content text-right right end">
                                <select v-model="Type" class="form-control center" id="sel1" required>
                                    <option v-if="active_signal.Action == null ">Go</option>
                                    <option v-if="active_signal.Action == null ">Cancell</option>
                                    <option v-if="active_signal.Action != null ">Martingale</option>
                                    <option v-if="active_signal.Action != null ">Martingale next signal</option>
                                    <option v-if="active_signal.Action != null ">Lose</option>
                                    <option v-if="active_signal.Action != null ">Win</option>
                                    <option v-if="active_signal.Action != null ">Back</option>
                                    <option v-if="active_signal.Result != null ">Done</option>
                                    <option>Delete</option>
                                </select>
                            </div>
                            <button class="btn btn grey">Action</button>
                        </form>
                    </div>
                </div>
            </li>

            <li v-if="vip_active_signal != false" class="collection-item" style="background-color:black;color:white">
                <div class="row col-md-12 center">
                    <div class="col-md-12">
                        <button class="btn btn white">
                        VIP
                    </button>
                    </div>
                    <div class="col-md-12" >
                        <img v-bind:src="vip_active_signal.A_flag" alt="">
                        {{vip_active_signal.Currency}}
                        <img v-bind:src="vip_active_signal.B_flag" alt="">
                    </div>
                    <div class="col-md-12" >
                       Signal type: {{vip_active_signal.Type}}
                    </div>
                    <div class="col-md-12" >
                      Action: {{vip_active_signal.Action}}
                    </div>
                    <div class="col-md-12" >
                      Martingale: {{vip_active_signal.Martingale}}
                    </div>
                    <div class="col-md-12" >
                      Result: {{vip_active_signal.Result}}
                    </div>
                    <div class="col-md-12">
                        <form @submit.prevent="updatesignal">
                            <div class="col-md-12 center">
                                <select v-model="Type" class="form-control center text-center" id="sel1" required>
                                    <option v-if="vip_active_signal.Action == null ">Go</option>
                                    <option v-if="vip_active_signal.Action == null ">Cancell</option>
                                    <option v-if="vip_active_signal.Action != null ">Martingale</option>
                                    <option v-if="vip_active_signal.Action != null ">Martingale next signal</option>
                                    <option v-if="vip_active_signal.Action != null ">Lose</option>
                                    <option v-if="vip_active_signal.Action != null ">Win</option>
                                    <option v-if="vip_active_signal.Action != null ">Back</option>
                                    <option v-if="vip_active_signal.Result != null ">Done</option>
                                    <option>Delete</option>
                                </select>
                            </div>
                            <button class="btn btn blue">Action</button>
                        </form>
                    </div>
                </div>
            </li>

            <li v-for="signal in signals"
            v-bind:key="signal.id" class="collection-item col-md-12 row center" style="background-color:grey">
            <div class="col-md-12 row">
              <div class="col-md-1">
                  <div class="first-content btn btn-success" v-if="signal.Result == 'Win' ">
                win
            </div>
            <div class="btn btn-danger red" v-else-if="signal.Result == 'Lose'">
                lose
            </div>
            <div class="btn btn-default white" v-else-if="signal.Result == 'Back'">
                Back
            </div>
             </div>
             <div class="col-md-8 ">
                 <img v-bind:src="signal.A_flag" alt="">
            {{signal.Currency}}
            <img v-bind:src="signal.B_flag" alt="">
            </div>  
            <div class="col-md-3">
                <router-link class=" btn btn-default" style="color:white;background-color : black" v-bind:to="{
                name:'view-signal',params: {signal_id:signal.id}}"
            >
            Show Signal
            </router-link>
            </div>
            </div>
            
            
            
            
            
            
            </li>
        </ul>

        <div class="fixed-action-btn" v-if="active_signal == false && vip_active_signal == false">
            <router-link to="/new-signal" class="btn-floating btn-large red">
            <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>
<<script>
import db from './firebaseInit'
export default {
    name: 'dashboard',
    data() {
        return {
            doc_id : null,
            signals: [],
            active_signal : false,
            vip_active_signal : false
        }
    },
    created(){
        db.collection('Signals').get().then(
            querySnapshot => {
                querySnapshot.forEach(
                    doc =>{
                        // console.log(doc.Curreny);
                        const data = {
                            'id' : doc.id,
                            'A_flag' :  doc.data().A_flag,
                            'B_flag' :  doc.data().B_flag,
                            'Action' :  doc.data().Action,
                            'Currency' : doc.data().Currency,
                            'Duration' : doc.data().Duration,
                            'Result' : doc.data().Result,
                            'Type' : doc.data().Type,
                            'enable' : doc.data().enable,
                            'gp' : doc.data().gp,
                            'Martingale' : doc.data().Martingale
                        }
                        if (doc.data().enable == true) {
                                if(doc.data().gp == "VIP"){
                                    this.vip_active_signal = data
                                    this.doc_id = data.id
                                }else{
                                    this.active_signal = data
                                    this.doc_id = data.id
                                }
                            }else{
                                
                        this.signals.push(data)
                            }
                    }
                )
            }
        )
    },
    methods: {
        updatesignal(){
            // console.log(this.doc_id)
            switch (this.Type) {
                case "Go":
                    db.collection('Signals').doc(this.doc_id).update({
                        Action : this.Type
                    }).then(
                        user=>{
                        this.$router.go({path: this.$router.path})
                        }
                    )
                    break;
                case "Cancell":
                    db.collection('Signals').doc(this.doc_id).update({
                        Action : this.Type
                    }).then(
                        user=>{
                        this.$router.go({path: this.$router.path})
                        }
                    )
                    break;
                case "Martingale":
                    db.collection('Signals').doc(this.doc_id).update({
                        Martingale : this.Type
                    }).then(
                        user=>{
                        this.$router.go({path: this.$router.path})
                        }
                    )
                    break;
                case "Martingale next signal":
                    db.collection('Signals').doc(this.doc_id).update({
                        Martingale : this.Type
                    }).then(
                        user=>{
                        this.$router.go({path: this.$router.path})
                        }
                    )
                    break;
                case "Lose":
                    db.collection('Signals').doc(this.doc_id).update({
                        Result : this.Type
                    }).then(
                        user=>{
                        this.$router.go({path: this.$router.path})
                        }
                    )
                    break;
                case "Win":
                    db.collection('Signals').doc(this.doc_id).update({
                        Result : this.Type
                    }).then(
                        user=>{
                        this.$router.go({path: this.$router.path})
                        }
                    )
                    break;
                case "Back":
                    db.collection('Signals').doc(this.doc_id).update({
                        Result : this.Type
                    }).then(
                        user=>{
                        this.$router.go({path: this.$router.path})
                        }
                    )
                    break;
                case "Done":
                    db.collection('Signals').doc(this.doc_id).update({
                        enable : false
                    }).then(
                        user=>{
                        this.$router.go({path: this.$router.path})
                        }
                    )
                    break;
                case "Delete":
                    db.collection('Signals').doc(this.doc_id).delete()
                    .then(
                        user=>{
                        this.$router.go({path: this.$router.path})
                        }
                    )
                    break;
                default:
                    break;
                }
            
            // setTimeout(function(){
            //         window.location.reload(1);
            //         }, 1000);        
            }
    }
}
</script>


